<template>
  <div class="home">
    <v-app id="inspire">
    <v-main>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-text-field 
            type="text" 
          class="search-bar" 
          placeholder="Search..."
          v-model="query"
          @keypress="fetchWeather">
              
            </v-text-field>
            <v-card class="mx-auto" max-width="400" v-if="typeof weather.main != 'undefined'" >
              
              <v-list-item two-line>
                <v-list-item-content >
                  <v-list-item-title  class="headline" >{{ weather.name }}</v-list-item-title>
                  
                  <v-list-item-subtitle >{{ weather_description }} </v-list-item-subtitle>
                  <v-list-item-subtitle >{{ dateBuilder() }} </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>

              <v-card-text>
                <v-row align="center">
                  <v-col class="display-3" cols="6">{{ Math.round(weather.main.temp) }}°c</v-col>
                  <v-col cols="6" v-if="weather_state == 'Rain'" >
                    <v-img
                    
                      src="../assets/rain.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                  <v-col cols="6" v-if="weather_state == 'Clouds'" >
                    <v-img
                    
                      src="../assets/clouds.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                  <v-col cols="6" v-if="weather_state == 'Clear'" >
                    <v-img
                    
                      src="../assets/sun.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                   
                  
                </v-row>
              </v-card-text>

              
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
  </div>
</template>

<script>

export default {
  name: "Home",
  props: {
    source: String,
  },
  data () {
    
    return {
    
    
      api_key: 'b48b08c4ed296d3355571a6fb1695a10',
      url_base: 'https://api.openweathermap.org/data/2.5/',
      query: '',
      weather: {},
      weather_state:null,
      weather_description:null,
    }
    
  },
  methods: {
    fetchWeather (e) {
      if (e.key == "Enter") {
        fetch(`${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`)
          .then(res => {
           
            return res.json();
            
          })
          .then(this.setResults);
      }
    },
    setResults (results) {
      this.weather = results
      this.weather_state = results.weather[0].main
      this.weather_description = results.weather[0].description
      
      console.log(results.weather)
    },
    dateBuilder () {
      let d = new Date();
      let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${day} ${date} ${month} ${year}`;
    }
  },
  
  
  
  created() {
    this.$vuetify.theme.dark = true;
  }
};

</script>
<style>
.v-card{
  background-image: url(https://wallpaperplay.com/walls/full/9/b/d/121428.jpg);
  background-size: cover;
  background-position: Top;
}

